import { set, pick, get } from '../utils';

import type { Adaptor } from '../types';

export const COORDIANTE_OPTIONS = [
  'radius',
  'innerRadius',
  'startAngle',
  'endAngle',
  'focusX',
  'focusY',
  'distortionX',
  'distortionY',
  'visual',
];

/**
 *  
 * , coordinate  coordinate  { type: 'theta' }  { type: 'radial' }
 *  options.coordinate 
 *  coordinate  options   innerRadius  endAngle ,  focusX distortionX
 * @param params Adaptor
 * @returns params Adaptor
 */
export function coordinate<P extends Adaptor>(params: P) {
  const { options } = params;
  const { coordinate = {} } = options;

  set(params, ['options', 'coordinate'], {
    ...coordinate,
    ...pick(options, COORDIANTE_OPTIONS),
    outerRadius: get(options, 'radius'),
  });

  return params;
}
