import { CHART_OPTIONS } from '../constants';
import { Options } from '../types';

/**
 * 
 */
export const deleteChartOptionKeys = <O extends Options>(options: O): O => {
  Object.keys(options).forEach((key) => {
    if (CHART_OPTIONS.includes(key)) {
      delete options[key];
    }
  });

  return options;
};
